import { BluetoothStruct } from "@/components/bluetooth";

/**   */
export class BaseModel
{
	/**
	 *  
	 * @param {any} th  
	 * @param {number} exec  API
	 * @param {number} len   API
	 * @param {BluetoothStruct} struct  
	 * @param {DataView} buf  
	 * @protected
	 */
	protected _set(th: any, exec: number, len: number, struct: BluetoothStruct, buf: DataView): boolean
	{
		try
		{
			if (buf.getUint8(0) === exec && buf.byteLength === len)
			{
				struct.decode(buf, th, 1);
				return true;
			}
		}
		catch (e)
		{
			console.log(e);
		}
		return false;
	}

	/**
	 *  
	 * @param {any} th  
	 * @param {number} exec  API
	 * @param {number} len   API
	 * @param {BluetoothStruct} struct  
	 */
	protected _get(th: any, exec: number, len: number, struct?: BluetoothStruct): DataView | undefined
	{
		try
		{
			const buf: DataView = new DataView(new ArrayBuffer(len));
			struct?.encode(buf, th, 1);
			buf.setUint8(0, exec);
			return buf;
		}
		catch (e)
		{
			console.log(e);
		}
	}

	/**
	 *      IBaseModel
	 * @param res
	 */
	setModel<T>(res: T): void
	{
		for (const key in res)
		{
			// @ts-ignore
			if (this[key] !== undefined) this[key] = res[key];
		}
	}
}
